<template>
  <v-container
    id="calendar"
    fluid
    tag="section"
  >
    <v-row>
      <v-col
        cols="7"
      >
        <v-row>
          <v-col
            cols="6"
            sm="3"
            md="3"
            style="padding-top: 0 !important; padding-bottom: 0 !important;"
          >
            <v-btn
              class="ma-2"
              outlined
              fab
              x-small
              color="primary"
              @click="prevDay"
            >
              <v-icon>mdi-arrow-left-bold</v-icon>
            </v-btn>
            <v-btn
              class="ma-2"
              outlined
              fab
              x-small
              color="primary"
              @click="nextDay"
            >
              <v-icon>mdi-arrow-right-bold</v-icon>
            </v-btn>
          </v-col>
          <v-col
            cols="6"
            sm="3"
            md="3"
            style="padding-top: 0 !important; padding-bottom: 0 !important;"
          >
            <v-menu
              ref="dialog"
              v-model="dialog"
              :close-on-content-click="false"
              :return-value.sync="today"
              transition="scale-transition"
              offset-y
              min-width="290px"
              max-width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="today"
                  label=" :"
                  readonly
                  v-bind="attrs"
                  prepend-icon="mdi-calendar-blank-multiple"
                  v-on="on"
                />
              </template>
              <v-date-picker
                v-model="today"
                no-title
                scrollable
              >
                <v-spacer />
                <v-btn
                  text
                  color="primary"
                  @click="dialog = false"
                >
                  Cancel
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="changePeriod({period: period, today: today}) && $refs.dialog.save(today)"
                >
                  OK
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>
          <v-col
            cols="6"
            sm="3"
            md="3"
            style="padding-top: 0 !important; padding-bottom: 0 !important;"
          >
            <v-select
              v-model="period"
              :items="periods"
              label=""
              @change="changePeriod({period: period, today: today})"
            />
          </v-col>
        </v-row>
      </v-col>
      <v-col
        cols="5"
        style="padding-top: 0 !important; padding-bottom: 0 !important;"
      >
        <v-card>
          <v-card-title><span style="font-size: 16px">  </span></v-card-title>
          <v-card-text
            small
          >
            <v-row>
              <v-col
                style="padding-top: 0 !important; padding-bottom: 0 !important;"
              >
                <span style="font-size: 13px">   : {{ inToday }}</span> <br>
                <span style="font-size: 13px"> : {{ moveOuts }}</span><br>
                <span style="font-size: 13px"> : {{ count }}</span><br>
                <span style="font-size: 13px"> : {{ freeRooms }}</span><br>
              </v-col>
              <v-col
                style="padding-top: 0 !important; padding-bottom: 0 !important;"
              >
                <span style="font-size: 13px"> : {{ closeRooms }}</span><br>
                <span style="font-size: 13px">: {{ paids }}</span><br>
                <span style="font-size: 13px"> : {{ notPaid }}</span><br>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <g-s-t-c
      ref="calendar"
      :config="config"
    />
    <v-dialog
      v-model="$store.state.bookingModal"
      max-width="800px"
      transition="dialog-bottom-transition"
    >
      <v-card>
        <question-reserve />
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="$store.state.reserve"
      max-width="1200px"
      transition="dialog-bottom-transition"
    >
      <v-card>
        <user-profile />
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="$store.state.toBook"
      max-width="1200px"
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-card-text>
          <to-book />
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="$store.state.popover"
      max-width="350px"
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-list>
          <v-list-item>
            <v-list-item-content>
              <v-list-item-title>{{$store.state.label}}</v-list-item-title>
              <v-list-item-subtitle>{{$store.state.rowLabel}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            text
            @click="$store.commit('OPEN_CLOSE_RESERVE', {status: 'pre-booking'})"
            v-if="$store.state.status==='reserve'"
          ></v-btn>
          <v-btn
            text
            @click="openCloseBook"
            v-if="$store.state.status==='booking' || $store.state.status==='half-booking'"
          ></v-btn>
          <v-btn
            text
            @click="$store.commit('OPEN_CLOSE_RESERVE', {status: 'populate'})"
            v-if="$store.state.status==='populate'"
          ></v-btn>
          <v-btn
            color="primary"
            text
            @click="$store.state.popover = false"
          > </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  import GSTC from 'vue-gantt-schedule-timeline-calendar'
  import UserProfile from './pages/UserProfile'
  import ToBook from './components/ToBook'
  import { mapMutations } from 'vuex'
  import QuestionReserve from './components/QuestionReserve'
  export default {
    name: 'App',
    components: {
      QuestionReserve,
      UserProfile,
      ToBook,
      GSTC,
    },
    data () {
      return {
        inToday: 13,
        moveOuts: 12,
        count: 45,
        freeRooms: 12,
        closeRooms: 25,
        paids: 15,
        notPaid: 9,
        popover: false,
        fav: true,
        message: false,
        hints: true,
        period: 30,
        periods: [{ text: '7', value: 7 }, { text: '15', value: 15 }, { text: '30', value: 30 }, { text: '90', value: 90 }],
        dayInZoom: [{ text: '1:1', value: 21 }, { text: '1:2', value: 22 }],
        zoomSize: 20,
        gstc: null,
        dialog: false,
        today: new Date().toISOString().substr(0, 10),
        openModal: false,
        text: '',
      }
    },

    computed: {
      config: function () {
        if (typeof this.$store.state.config.actions === 'undefined') {
          this.$store.commit('ADD_ACTIONS')
        }
        return this.$store.state.config
      },
    },
    methods: {
      ...mapMutations({
        setBookingOpen: 'SET_BOOKING_OPEN',
        changeCurrentDate: 'CHANGE_CURRENT_DATE',
        setRefs: 'SET_REFS',
        prevDay: 'PREV_DAY',
        nextDay: 'NEXT_DAY',
        changePeriod: 'CHANGE_PERIOD',
        openCloseBook: 'OPEN_CLOSE_BOOK',
      }),
    },
  }
</script>
<style>
  .v-card {
    margin-top: 0 !important;
  }
  .v-card__title {
    line-height: 1rem !important;
  }
  .highlight {
    background-color: #ffff00;
  }
  .gantt-schedule-timeline-calendar__chart-timeline-items-row-item {
    border-radius: 0 !important;
    margin-top: 1px !important;
    height: 37px !important;
    padding: 8px 0 !important;
    border: 1px solid black;
  }
  .gantt-schedule-timeline-calendar__chart-calendar-date--level-0 .gantt-schedule-timeline-calendar__chart-calendar-date-content {
    margin: 7px !important;
  }
  .weekends-highlight {
    background-color: rgba(255, 196, 126, 0.5);
  }
  .gantt-schedule-timeline-calendar__chart-calendar-date--month {
    border-left: 2px solid rgba(107, 107, 107, 0.75) !important;
  }
  .gantt-schedule-timeline-calendar__chart-timeline-grid-row-block, .gantt-schedule-timeline-calendar__list-column-header-content, .gantt-schedule-timeline-calendar__list-column-row, .gantt-schedule-timeline-calendar__chart-calendar-date{
    border-right: 1px solid rgba(107, 107, 107, 0.75) !important;
    border-bottom: 1px solid rgba(107, 107, 107, 0.75) !important;
  }
  .gantt-schedule-timeline-calendar__chart-calendar-date-content.gstc-date-small {
    line-height: 15px !important;
  }
  .gantt-schedule-timeline-calendar__chart-calendar-date-content--day, .gantt-schedule-timeline-calendar__chart-calendar-date-content--week {
    line-height: 15px !important;
  }
</style>
